<!--CusTable组件doc-->
<template>
    <div style="width: 100%" class="cus-table">
        <el-table style="width: 100%" :data="tableData" :header-cell-style="{background:'#E6EAFE',color:'#606266'}">
            <el-table-column type="index" label="序号" width="90" />
            <el-table-column label="所属公司" prop="kcName" show-overflow-tooltip></el-table-column>
            <el-table-column label="所属平台" prop="kpName"></el-table-column>
            <el-table-column label="所属路线" prop="glName"></el-table-column>
            <el-table-column label="计量点名称" prop="jpName" show-overflow-tooltip></el-table-column>
            <el-table-column label="采集时间" prop="ts" width="200">
                <template slot-scope="scope">
                    <span>{{changeTime(scope.row['ts'])}}</span>
                </template>
            </el-table-column>
            <el-table-column label="正向有功表码值" prop="ma" width="150">
                <template slot-scope="scope">
                    <span>{{scope.row['ma']?scope.row['ma']:"-" }}</span>
                </template>
            </el-table-column>
        </el-table>
        <div class="page pagination-box">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="pageNum"
                :page-sizes="[10, 20, 50, 100]" :page-size="pageSize" layout="prev, pager, next, jumper,total, sizes"
                :total="total">
            </el-pagination>
        </div>
    </div>
</template>
<script>
    import {
        parseTime
    } from "@/utils/index";
    import Table from "@/components/ElTableCommon.vue";
    export default {
        extends: Table,
        name: 'ReadinngCusTable',
        data() {
            return {}
        },
        methods: {
            changeTime(t) {
                return parseTime(new Date(t), '{y}/{m}/{d} {h}:{i}')
            }
        }
    }
</script>

<style scoped lang="scss">
    .page {
        display: flex;
        justify-content: flex-end;
    }

    .cus-btn {
        color: #2E4AF7;
        font-size: 14px;
        padding: 0 2px;
        cursor: pointer;
    }

    /deep/ .active-row {
        background-color: #F5F7FA;
    }
</style>