<!--抄表读数-->
<template>
    <BoxTitle>
        <SearchInputs @downHand="downHand" @searchHand="searchHand"></SearchInputs>
        <CusTable :tableData="tableData" @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange"
            :pageNum="page.pageNum" :pageSize="page.pageSize" :total="total">
        </CusTable>
    </BoxTitle>
</template>
<script>
    //组件
    import TableCommon from "@/components/TableCommon.vue";
    import CusTable from "./components/ReadinngCusTable.vue";
    import SearchInputs from "./components/SearchInputsReading.vue";
    import BoxTitle from "@/components/BoxTitle.vue";
    //接口
    import {
        getCamList,
        exportMeterRead,
    } from "@/api/archives.js";
    //公用函数
    import tool from "@/utils/tool.js";

    export default {
        extends: TableCommon,
        name: 'reading',
        components: {
            CusTable,
            SearchInputs,
            BoxTitle,
        },
        mounted() {},
        methods: {
            _getData(params = {}) { // 通过接口 获取最新数据 如果无接口 则是第一次加载
                if (!this.search.jpCode) return;
                getCamList({
                    ...this.search,
                    ...this.page
                }).then(res => {
                    const data = res.rows ? res.rows : [];
                    this.tableData = data;
                    this.total = res.total;
                }).catch(err => {
                    this.tableData = [];
                    this.total = 0;
                })
            },
            //导出按钮
            downHand(data) {
                exportMeterRead({
                    ...this.search,
                }).then(tool.down)
            },
        }
    }
</script>