<template>
    <div class="search" @keyup.enter="search">
        <form-create v-model="formObj" :rule="rule" :option="options" @btn-search="search" @kp-code-select="selectKp"
            @kp-code-clear="clearKp" @btn-reset="reset" @jp-callback="search">
        </form-create>
        <div class="li">
            <el-button type="primary" @click="down">导 出</el-button>
        </div>
    </div>
</template>
<script>
    //接口
    import {
        getKpList,
        getKcList,
        getPointsList
    } from "@/api/archives.js";
    import {
        parseTime,
        getPrevMonth,
        daySE
    } from "@/utils/index";

    import form from "../billConfirm/components/billCreate/SearchInputsCreate.vue";
    export default {
        extends: form,
        data() {
            return {
                first: false, //记录第一次加载提醒
                defaultPlatName: '阳101H91', //默认平台
                //表单生成规则
                rule: [{
                    type: 'select',
                    field: "kcCode",
                    title: '公司名称：',
                    value: '',
                    options: [],
                    props: {
                        filterable: true,
                    },
                    on: {
                        change: (item) => {
                            //清空平台 计量点 线路
                            const formData = this.formObj.bind();
                            formData.glCode = '';
                            if (this._findRule('kpCode')) this.formObj.exec('kpCode', 'clear')
                            if (this._findRule('jpCode')) this.formObj.exec('jpCode', 'clear')
                        }
                    },
                    col: {
                        span: 6
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'kpCode',
                    title: '平台名称：',
                    props: {
                        placeholder: '请选择平台或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        value: '',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                kcCode: this.formObj.getValue('kcCode'),
                                _loading: false, //不显示加载中图标
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getKpList(opt).then(res => {
                                if (res.data) {
                                    const list = res.data.map(item => ({
                                        ...item,
                                        value: item.name,
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    emit: ['select', 'clear'],
                    col: {
                        span: 6
                    }
                }, {
                    type: 'AutoCompleteQuery',
                    field: 'jpCode',
                    title: '计量点名称：',
                    value: '',
                    props: {
                        placeholder: '请选择计量点或输入名称搜索',
                        fieldValue: 'code',
                        fieldLabel: 'name',
                        queryFunc: (keyWord) => {
                            const opt = {
                                pageNum: 1,
                                pageSize: 20,
                                _loading: false,
                            }
                            if (keyWord !== '') {
                                opt.jpName = keyWord;
                            }
                            const {
                                kpCode,
                                kcCode,
                                glCode
                            } = this.formObj.formData();
                            if (kpCode !== '') {
                                opt.kpCode = kpCode;
                            }
                            if (kcCode !== '') {
                                opt.kcCode = kcCode;
                            }
                            if (keyWord !== '') {
                                opt.keyWord = keyWord;
                            }
                            return getPointsList(opt).then(res => {
                                if (res.list) {
                                    const list = res.list.rows.map(item => ({
                                        ...item,
                                        value: item.name
                                    }))
                                    return list
                                } else {
                                    return []
                                }
                            })
                        }
                    },
                    emit: ['callback'],
                    emitPrefix: 'jp',
                    col: {
                        span: 7,
                    }
                }, {
                    type: 'SubmitBtns',
                    field: 'btn',
                    emit: ['search', 'reset'],
                    col: {
                        span: 5
                    }
                }, {
                    type: "DatePicker",
                    field: "time",
                    title: "时间选择：",
                    value: daySE(),
                    props: {
                        type: "datetimerange",
                        format: "MM-dd HH:mm",
                        placeholder: "请选择时间范围",
                        pickerOptions: {
                            disabledDate(time) {
                                const now = new Date();
                                now.setDate(now.getDate() + 1);
                                now.setMinutes(0);
                                now.setSeconds(0);
                                now.setMilliseconds(0);
                                return time.getTime() > now;
                            },
                        }
                    },
                    col: {
                        span: 6
                    }
                }]
            }
        },
        mounted() {
            this.getPlat()
        },
        methods: {
            getPlat() {
                getKpList({
                    kcCode: 'KC0001',
                    _loading: false, //不显示加载中图标
                }).then(res => {
                    const defaultPlat = res.data.find(item => item.name == this.defaultPlatName);
                    this.formObj.setValue({
                        kpCode: defaultPlat.code
                    })
                    this.formObj.exec('kpCode', 'setLabel', defaultPlat.name);
                    this.formObj.exec('jpCode', 'selectOne', 0, true);
                })
            },
            selectKp() { //选择平台 默认选择第一条计量点
                this.formObj.exec('jpCode', 'selectOne');
            },
            clearKp() { //清空平台 计量点清空
                if (this._findRule('jpCode')) this.formObj.exec('jpCode', 'clear')
            },
            search() {
                const formData = this.formObj.formData();
                if (!formData.jpCode) {
                    if (!this.first) {
                        this.first = true;
                        return;
                    } else {
                        this.$message({
                            message: '请选择一个计量点',
                            type: 'warning',
                        })
                        return;
                    }
                }
                const opt = {
                    jpCode: formData.jpCode,
                    st: parseTime(formData.time[0], '{y}/{m}/{d} {h}:{i}:{s}'),
                    et: parseTime(formData.time[1], '{y}/{m}/{d} {h}:{i}:{s}'),
                }
                for (let key in opt) {
                    if (opt[key] == '' || opt[key] == undefined) delete opt[key];
                }
                this.$emit('searchHand', opt);
            },
        }
    }
</script>